*{
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 60px;
}

input{
    font-size: 1.2rem;
    width: 100%;
    color: blue;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;

}

.card p{
    padding: 5px;
}

.card img{
    max-height: 170px;
}